.cat_MainDiv{
    width: 90%;
    padding: 2px;
    background-color: rgb(255, 255, 255);
    display: inline-flex;
    flex-direction: column;
    z-index: 1;
    margin: 5px auto;
    box-shadow: 0px 0px 2px;

}

.cat_img{
    width: 160px;
    height: 150px;
}

.cat_name{
    margin-left: 5px;
    text-align: start;
    font-size: 15px;
}

.cat_pricTxt{
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 5px;
    text-align: start;
}
.cat_AddBtnDiv{
    background-color: rgb(241, 241, 241);
    text-align: center;
}

.cat_kg{
    text-align: start;
    margin-left: 5px;
    margin-top: 10px;
}

.catBtn{
    cursor: pointer;
    background-color: orange;
    border: 0.5px solid;
    margin-left: auto;
    margin-right: auto;
    padding: 5px;
    padding-left: 50px;
    padding-right: 50px;
    margin-bottom: 5px;
    margin-top: 5px;
}